.filmPage {
  text-align: right;
  display: flex;
  justify-content: space-around;
}

.main {
  color: #6b828f;
  width: 530px;
}

b {
  color: rgba(246, 248, 246, 0.678);
}

.title {
  margin-top: 100px;
  margin-bottom: 30px;
  color: #fff;
  font-size: 30px;
  font-weight: 900;
  text-align: center;
}

.date,
.vote,
.language,
.overview {
  margin-bottom: 10px;
}

.overview {
  text-align: justify;
}

.overviewTitle {
  display: block;
  text-align: center;
}

.filmImage {
  margin-top: 100px;
  width: 340px;
  height: 440px;
}

.button {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  margin-bottom: 50px;
}



.watchedButton {
  fill: #1e8a71;
}

.deleteWatchedButton {
  fill: #cf7a05;
  transform: rotate(-360deg);
}

.favButton {
  fill: #1e8a71;
}

.deleteFavButton {
  fill: #cf7a05;
  transform: rotate(-360deg);
}

.favButton,
.deleteFavButton {
  height: 70px;
  width: 70px;
}

.favButton,
.deleteFavButton,
.deleteWatchedButton,
.watchedButton {
  padding: 3px;
  outline: none;
  transition: 1s ease-in;
}

.deleteWatchedButton,
.watchedButton {
  height: 73px;
  width: 73px;
}

.favButton:hover,
.deleteFavButton:hover,
.deleteWatchedButton:hover,
.watchedButton:hover {
  padding: 0;
}

.buttonWrapper {
  margin-top: 35px;
  display: flex;
  justify-content: space-around;
}
